@use 'base/variables' as v;
@use 'base/mixins' as m;

.table {
    width: 100%;
    border-collapse: collapse;

    &__thead {
    background-color: v.$gris;
    color: v.$blanco;
    }

    &__th {
        padding: 1rem;
        text-align: center;
    }
    &__tr {
        &:nth-child(even) {
            background-color: v.$grisClaro;
        }
    }
    &__td {
        padding: 1.5rem 1rem;
        text-align: center;

        &--acciones {
            @extend .table__td;
            display: flex;
            justify-content: flex-end;
            gap: 2rem;
            align-items: center;
        }
    }
    &__accion {
        all: unset;
        text-transform: uppercase;
        font-size: 1.4rem;
        font-weight: v.$bold;

        &--editar {
            color: v.$azulFuerte;
        }
        &--eliminar {
            color: v.$rojo;
        }

        &:hover {
            cursor: pointer;
        }
    }
    .estatus-activo {
        color: v.$verde; 
        font-weight: bold; 
    }

    .estatus-inactivo {
        color: v.$rojo; 
        font-weight: bold;
    }

    .estatus-desconocido {
        color: v.$gris;
        font-weight: bold;
    }
}