@use 'base/mixins' as m;
@use 'base/variables' as v;

.formulariocontacto {
    p {
        font-size: 2rem;
        color: v.$black;
        margin: 2rem 0 2rem 0;
    }

    legend {
        font-size: 2rem;
        color: v.$black;
    }

    label {
        font-weight: bold;
        text-transform: uppercase;
        display: block;
    }
    input:not([type="submit"]), 
    textarea,
    select {
        padding: 1rem;
        display: block;
        width: 100%;
        background-color: v.$blanco;
        border: 1px solid v.$rosa2;
        border-radius: 2rem;
        margin-bottom: 2rem;
    }

    textarea {
        height: 20rem;
    }

    .forma-contacto {
        max-width: 30rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    input[type="radio"] {
        margin: 0;
        width: auto;
    }

    .enviar {
        margin-top: 3rem;
    }
}